.step-container{
    height: fit-content;
    background-color: #B8D59C;
    padding: 10px;
    position: relative;
    overflow: hidden;
   &.dark{
    .flex-container{
        .text-wrapper{
            h1{
                color: #ffffff;
            }
            p{
                color: #ffffff;
            }
        }
        }
   }
    .flex-container{
        display: flex;
        align-items: center;
        position: relative;
        z-index: 4;
        .text-wrapper{
            padding: 20px ;
            position: relative;
            z-index: 4;
            h1{
                font-size: 85px;
                color: #212121;
                font-weight: 600;
            }
            p{
                font-size: 35px;
                color: #272727;
                margin-top: 15px;
                margin-left: 35px;

            }
            flex: 5;
        }
        .img-wrapper{
            flex: 7;
            display: flex;
            justify-content: center;
           
            .belt{
                position: absolute;
                background-color: #FEEB5E;
                width: 150%;
                height: 250px;
                transform: rotate(55deg);
                z-index: 3;
                top: 0;
                left: -30%;
            }
            img{
                position: relative;
                z-index: 4;
                width: 450px;
                height: 450px;

            }
        }
    }
    
}
@media screen and (max-width:825px) {
    .step-container{
        .flex-container{
            .img-wrapper{
                flex: 6 !important;
                img{
                    width: 350px;
                    height: 350px;
    
                }
               
            }
            .text-wrapper{
                flex: 6 !important;
                padding: 10px ;
                h1{
                    font-size: 70px;
                }
                p{
                    font-size: 30px;
                    margin-left: 25px;
    
                }
            }
        }
    }
}

@media screen and (max-width:630px) {
    .step-container{
        .flex-container{
            flex-direction: column;
            .img-wrapper{
                order:1;
                img{
                    width: 350px;
                    height: 350px;
    
                }
                .belt{
                    display: none;
                }
            }
            .text-wrapper{
               order: 2;
                padding: 10px ;
                h1{
                    font-size: 70px;
                }
                p{
                    font-size: 30px;
                    margin-left: 25px;
    
                }
            }
        }
    }
}